import React, { useEffect, useState } from "react";
import {
  TraceListDummyData,
  UserDummyData,
  ReplyDummyData,
  MyInfoDUmmyDatas,
  BookMarkDummyData,
} from "../../utils/dummyDatas";

import {
  UserPostBookMarksDiv,
  UserPostCreateAtDiv,
  UserPostDelBtn,
  UserPostGoodsDiv,
  UserPostsFilterSelector,
  UserPostsFilterIcon,
  UserPostsListDiv,
  UserPostsTitleDiv,
  UserPostsTopDiv,
  UserPostsWrapper,
  UserPostTitleDiv,
  UserPostViewsDiv,
} from "../../styles/components/UserPostsStyled";
import Cookies from "js-cookie";
import SortIcon from "@mui/icons-material/Sort";
import TurnedInIcon from "@mui/icons-material/TurnedIn";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import ThumbUpOffAltIcon from "@mui/icons-material/ThumbUpOffAlt";

import BookmarkBorderIcon from "@mui/icons-material/BookmarkBorder";
import axios from "axios";

const UserBookMarks = () => {
  const [myPosts, setMyPosts] = useState([]);
  const [viewPosts, setViewPosts] = useState();
  const [myBookMark, setMyBookMark] = useState([]);
  const accessToken = Cookies.get("accessToken");

  const handleTraceUnBookMakr = (id) => {
    axios
      .delete(
        `https://api.stackflov.com/bookmarks`, //  API  URL
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${accessToken}`,
          },
          withCredentials: true,
          // DELETE   body  'data'    .
          data: {
            boardId: id,
          },
        }
      )
      .then((response) => {
        console.log("  :", response.data);
        //   UI      .
        alert(" .");
      })
      .catch((error) => {
        console.error("  :", error);
        //          .
        if (error.response) {
          //     2xx   
          alert(
            `: ${
              error.response.data.message || "  ."
            }`
          );
        } else {
          //        
          alert("  .  .");
        }
      });
  };

  useEffect(() => {
    axios
      .get("https://api.stackflov.com/bookmarks/my", {
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        withCredentials: true, //   allowCredentials(true)  
      })
      .then((res) => {
        const test = res.data;
        setMyBookMark(test);
      });
  }, []);

  useEffect(() => {
    const view = myBookMark.map((item) => {
      return (
        <UserPostsListDiv>
          <UserPostCreateAtDiv>
            {item.createdAt.slice(0, 10)}
          </UserPostCreateAtDiv>
          <UserPostTitleDiv>{item.boardTitle}</UserPostTitleDiv>
          {/* <UserPostViewsDiv>
            <RemoveRedEyeIcon />
            {item.views}
          </UserPostViewsDiv>
          <UserPostGoodsDiv>
            <ThumbUpOffAltIcon />
            {item.good}
          </UserPostGoodsDiv> */}
          <UserPostBookMarksDiv
            onClick={() => {
              handleTraceUnBookMakr(item.boardId);
            }}
          >
            <TurnedInIcon style={{ fontSize: "40px" }} />
            {item.bookMark}
          </UserPostBookMarksDiv>
          {/* <UserPostDelBtn></UserPostDelBtn> */}
        </UserPostsListDiv>
      );
    });
    setViewPosts(view);
  }, [myBookMark]);
  return (
    <UserPostsWrapper>
      <UserPostsTopDiv>
        <UserPostsTitleDiv> </UserPostsTitleDiv>
        <UserPostsFilterIcon>
          <SortIcon />
        </UserPostsFilterIcon>
        <UserPostsFilterSelector>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </UserPostsFilterSelector>
      </UserPostsTopDiv>
      {viewPosts}
    </UserPostsWrapper>
  );
};

export default UserBookMarks;
