import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import api from "../../utils/api";
import {
  PageWrap, Header, Title, Sub,
} from "../../styles/components/admin/AdminCommonStyled";
import {
  CardsGrid, StatCard, CardTitle, CardValue,
  LinkRow, LinkBtn
} from "../../styles/components/admin/AdminDashboardStyled";

const fmt = (n) => (typeof n === "number" ? n.toLocaleString() : "-");

export default function AdminDashboard() {
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(true);
  const [err, setErr] = useState(null);

  useEffect(() => {
    let alive = true;
    setLoading(true);
    setErr(null);

    api.get("/admin/dashboard/stats")
      .then(({ data }) => { if (alive) setStats(data || {}); })
      .catch((e) => {
        if (alive) setErr(e?.response?.data?.message || e.message || " ");
      })
      .finally(() => { if (alive) setLoading(false); });

    return () => { alive = false; };
  }, []);

  if (loading) return <PageWrap>  </PageWrap>;
  if (err)      return <PageWrap style={{ color:"#c00" }}>: {String(err)}</PageWrap>;

  const {
    totalUsers, activeUsers, totalBoards, totalComments,
    totalReports, pendingReports, todayNewUsers, todayNewBoards,
  } = stats || {};

  return (
    <PageWrap>
      <Header>
        <Title> </Title>
        <Sub>     </Sub>
      </Header>

      {/*   */}
      <CardsGrid>
        <DashCard title=" "    value={fmt(totalUsers)}   to="/admin/users" />
        <DashCard title=" "   value={fmt(activeUsers)} />
        <DashCard title=" "     value={fmt(totalBoards)}  to="/admin/boards" />
        <DashCard title=" "       value={fmt(totalComments)} />
      </CardsGrid>

      {/*   */}
      <CardsGrid style={{ marginTop: 16 }}>
        <DashCard title=" "   value={fmt(totalReports)}  to="/admin/reports" />
        <DashCard title="  " value={fmt(pendingReports)} to="/admin/reports" warn />
        <DashCard title=" "   value={` ${fmt(todayNewUsers)} /  ${fmt(todayNewBoards)}`} />
      </CardsGrid>

      {/*   */}
      <LinkRow>
        <LinkBtn to="/admin/users"> </LinkBtn>
        <LinkBtn to="/admin/boards"> </LinkBtn>
        <LinkBtn to="/admin/reports"> </LinkBtn>
        <LinkBtn to="/admin/banned-words"> </LinkBtn>
        <LinkBtn to="/admin/dashboard/detailed"> </LinkBtn>
        <LinkBtn to="/admin/notices"> </LinkBtn>
      </LinkRow>
    </PageWrap>
  );
}

function DashCard({ title, value, to, warn }) {
  const body = (
    <StatCard $warn={warn}>
      <CardTitle>{title}</CardTitle>
      <CardValue>{value}</CardValue>
    </StatCard>
  );
  return to ? <Link to={to} style={{ textDecoration:"none" }}>{body}</Link> : body;
}
