import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "../../styles/components/Button";
import { FilePicker } from "../../styles/components/FilePicker";
import {
  TraceCategoryDiv,
  TraceCategoryTitle,
  TraceCreateTopContent,
  TraceCreateWrapper,
  TraceTitleDiv,
  TraceTitleInputDiv,
  TraceCategorySelectorItem,
  TraceCreateBottomContent,
  TraceCreateMiddleContent,
  TraceCreateContentInput,
  TraceCreateCancleBtn,
  TraceCreateBtn,
} from "../../styles/components/TraceCreateFormStyled";
import Cookies from "js-cookie";
import axios from "axios";

const TraceCreateForm = () => {
  //   : 0=,1=,2=,3= ( 1~4  0~3 )
  const [selectCategory, setSelectCategory] = useState(0);
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");

  //  
  const [files, setFiles] = useState([]);         // File[] ()
  const [previews, setPreviews] = useState([]);   //  URL 

  const navigator = useNavigate();
  const accessToken = Cookies.get("accessToken");

  useEffect(() => {
    if (!accessToken) {
      alert("  .");
      navigator("/login");
    }
  }, [accessToken, navigator]);

  //  
  const handleSelectImages = (e) => {
    const picked = Array.from(e.target.files || []);
    if (!picked.length) return;

    // () / 
    const MAX_FILES = 10;
    const MAX_MB = 10;
    const filtered = [];
    for (const f of picked) {
      const tooBig = f.size / 1024 / 1024 > MAX_MB;
      if (tooBig) {
        alert(` ${f.name}  ${MAX_MB}MB .`);
        continue;
      }
      filtered.push(f);
    }

    //   ,  (+ )
    const merged = [...files, ...filtered].slice(0, MAX_FILES);
    const dedup = [];
    const sig = new Set();
    for (const f of merged) {
      const key = `${f.name}-${f.size}`;
      if (!sig.has(key)) {
        sig.add(key);
        dedup.push(f);
      }
    }

    setFiles(dedup);
  };

  //  /
  useEffect(() => {
    const urls = files.map((f) => URL.createObjectURL(f));
    setPreviews(urls);
    return () => urls.forEach((u) => URL.revokeObjectURL(u));
  }, [files]);

  //  
  const removeImage = (idx) => {
    setFiles((prev) => prev.filter((_, i) => i !== idx));
  };

  //  (multipart/form-data)
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!title.trim() || !content.trim()) {
      alert("   .");
      return;
    }

    const formData = new FormData();

    //    "data" JSON 
    const dto = {
      title: title.trim(),
      content: content.trim(),
      category: Number(selectCategory), // 0~3
    };
    formData.append(
      "data",
      new Blob([JSON.stringify(dto)], { type: "application/json" })
    );

    //    (  : @RequestPart(required = false))
    for (const f of files) {
      formData.append("images", f);
    }

    try {
      const res = await axios.post(
        "https://api.stackflov.com/boards/multipart",
        formData,
        {
          headers: {
            Authorization: `Bearer ${accessToken}`, // Content-Type  X ( boundary   )
          },
          withCredentials: true,
        }
      );

      // Location       
      const loc = res.headers?.location || res.headers?.Location;
      if (loc) navigator(loc);
      else navigator("/");
    } catch (error) {
      const msg =
        error.response?.data?.message ||
        error.response?.data ||
        error.message;
      console.error("  :", error.response?.status, msg);

      //   
      if (
        typeof msg === "string" &&
        msg.includes(" ")
      ) {
        alert(msg);
      } else {
        alert("  .");
      }
    }
  };

  return (
    <TraceCreateWrapper>
      <TraceCreateTopContent>
        <TraceTitleDiv></TraceTitleDiv>
        <TraceTitleInputDiv
          placeholder="  ."
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
      </TraceCreateTopContent>

      <TraceCategoryDiv>
        <TraceCategoryTitle></TraceCategoryTitle>

        <TraceCategorySelectorItem
          selectCategory={selectCategory === 0}
          onClick={() => setSelectCategory(0)}
        >
           
        </TraceCategorySelectorItem>

        <TraceCategorySelectorItem
          selectCategory={selectCategory === 1}
          onClick={() => setSelectCategory(1)}
        >
           
        </TraceCategorySelectorItem>

        <TraceCategorySelectorItem
          selectCategory={selectCategory === 2}
          onClick={() => setSelectCategory(2)}
        >
           
        </TraceCategorySelectorItem>

        <TraceCategorySelectorItem
          selectCategory={selectCategory === 3}
          onClick={() => setSelectCategory(3)}
        >
           
        </TraceCategorySelectorItem>
      </TraceCategoryDiv>

      <TraceCreateMiddleContent>
        <TraceCreateContentInput
          placeholder="  ."
          value={content}
          onChange={(e) => setContent(e.target.value)}
        />

        <div style={{ marginTop: 16, display: "flex", alignItems: "center", gap: 12, flexWrap: "wrap" }}>
      <FilePicker>
          
        <input
          type="file"
          accept="image/*"
          multiple
          onChange={handleSelectImages}
        />
      </FilePicker>

      {/*    */}
      {files.length > 0 && (
        <span style={{ color: "#6B7280", fontSize: 14 }}>
          {files.length} 
        </span>
      )}
    </div>

    {/*   */}
    <div style={{ display: "flex", gap: 10, flexWrap: "wrap", marginTop: 10 }}>
      {previews.map((src, idx) => (
        <div key={idx} style={{
          position: "relative", width: 96, height: 96,
          borderRadius: 12, overflow: "hidden", border: "1px solid #E5E7EB",
          boxShadow: "0 8px 18px rgba(0,0,0,.06)"
        }}>
          <img src={src} alt={`preview-${idx}`} style={{ width: "100%", height: "100%", objectFit: "cover" }} />
          <button
            type="button"
            onClick={() => removeImage(idx)}
            style={{
              position: "absolute", top: 6, right: 6,
              background: "rgba(17,24,39,.7)", color: "#fff",
              border: 0, borderRadius: 8, fontSize: 12, padding: "4px 6px", cursor: "pointer"
            }}
          >
            
          </button>
        </div>
      ))}
    </div>
      </TraceCreateMiddleContent>

      <TraceCreateBottomContent>
        <TraceCreateCancleBtn onClick={handleSubmit}>
           
        </TraceCreateCancleBtn>

        <TraceCreateBtn onClick={() => navigator("/")}>
           
        </TraceCreateBtn>
      </TraceCreateBottomContent>
    </TraceCreateWrapper>
  );
};

export default TraceCreateForm;
