import React, { useEffect, useState } from "react";
import Cookies from "js-cookie";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import styled from "styled-components"; // 1. styled-components 
import {
  NiBangNeBangCategoryDiv,
  NiBangNeBangCategoryTitle,
  NiBangNeBangCreateTopContent,
  NiBangNeBangCreateWrapper,
  NiBangNeBangTitleDiv,
  NiBangNeBangTitleInputDiv,
  NiBangNeBangCategorySelectorItem,
  NiBangNeBangCreateBottomContent,
  NiBangNeBangCreateMiddleContent,
  NiBangNeBangCreateContentInput,
  NiBangNeBangCreateCancleBtn,
  NiBangNeBangCreateBtn,
} from "../../styles/components/NiBangNeBangCreateFormStyled"; // <--   
import NiBangNeBangStarRating from "./NiBangNeBangStarRating"; // <--   

// ---   ---
//     
const ImagePreviewWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 15px;
  margin-bottom: 20px;
`;

const ImagePreview = styled.img`
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 4px;
`;
// ---    ---

const NiBangNeBangCreateForm = () => {
  const [title, setTitle] = useState(""); // 2. undefined -> ""
  const [address, setAddress] = useState(""); // undefined -> ""
  const [content, setContent] = useState(""); // undefined -> ""
  const [rating, setRating] = useState(0);
  const [images, setImages] = useState([]); // 3.  state 
  const [previews, setPreviews] = useState([]); //  URL state
  const navigator = useNavigate();

  const accessToken = Cookies.get("accessToken");

  useEffect(() => {
    if (accessToken == undefined) {
      alert("  .");
      navigator("/login");
    }
  }, [accessToken, navigator]); // 4. navigator dependency array 

  // 5.   
  const handleImageChange = (e) => {
    const files = Array.from(e.target.files);
    setImages(files);

    //   URL  (  )
    previews.forEach((url) => URL.revokeObjectURL(url));

    //   
    const newPreviews = files.map((file) => URL.createObjectURL(file));
    setPreviews(newPreviews);
  };

  const handlePost = async (e) => {
    e.preventDefault();

    if (!title || !content || !address || rating === 0) {
      alert(", , ,   .");
      return;
    }

    const formData = new FormData();

    // 6. FormData  ("data" -> "dto")
    const data = {
      title,
      address: address,
      content,
      rating: rating,
    };
    formData.append(
      "data", //  @RequestPart("dto") 
      new Blob([JSON.stringify(data)], { type: "application/json" })
    );

    // 7. images state 
    if (images && images.length > 0) {
      images.forEach((file) => {
        if (file) formData.append("images", file);
      });
    }

    try {
      const response = await axios.post(
        "https://api.stackflov.com/map/reviews",
        formData,
        {
          headers: {
            // 8. Content-Type  (  )
            Authorization: `Bearer ${accessToken}`,
          },
          withCredentials: true,
        }
      );
      console.log(":", response);
      navigator("/nibangnebang");
    } catch (error) {
      console.error(
        "  :",
        error.response?.status,
        error.response?.data || error.message
      );
      alert("  .");
    }
  };

  return (
    <NiBangNeBangCreateWrapper>
      <NiBangNeBangCreateTopContent>
        <NiBangNeBangTitleDiv></NiBangNeBangTitleDiv>
        <NiBangNeBangTitleInputDiv
          placeholder="  ."
          value={title}
          onChange={(e) => {
            setTitle(e.target.value);
          }}
        />
      </NiBangNeBangCreateTopContent>
      <NiBangNeBangCreateMiddleContent>
        <NiBangNeBangCreateContentInput
          placeholder="  ."
          value={content}
          onChange={(e) => {
            setContent(e.target.value);
          }}
        />
      </NiBangNeBangCreateMiddleContent>
       :
      <input
        style={{ marginLeft: "20px" }}
        placeholder="ex) "
        value={address} // 9. value 
        onChange={(e) => {
          setAddress(e.target.value);
        }}
      />
      <NiBangNeBangStarRating value={rating} onChange={setRating} />

      {/* --- 10.     --- */}
      <div style={{ margin: "20px 0" }}>
        <input
          type="file"
          multiple
          accept="image/*"
          onChange={handleImageChange}
          style={{ marginTop: "10px" }}
        />
      </div>

      {/* --- 11.   --- */}
      <ImagePreviewWrapper>
        {previews.map((previewUrl, index) => (
          <ImagePreview
            key={index}
            src={previewUrl}
            alt={` ${index + 1}`}
          />
        ))}
      </ImagePreviewWrapper>
      {/* ---   --- */}

      <NiBangNeBangCreateBottomContent>
        {/* --- 12.    --- */}
        <NiBangNeBangCreateBtn //  
          onClick={(e) => {
            handlePost(e);
          }}
        >
           
        </NiBangNeBangCreateBtn>
        <NiBangNeBangCreateCancleBtn //  
          onClick={() => {
            if (window.confirm(" ?")) {
              navigator("/nibangnebang");
            }
          }}
        >
           
        </NiBangNeBangCreateCancleBtn>
      </NiBangNeBangCreateBottomContent>
    </NiBangNeBangCreateWrapper>
  );
};

export default NiBangNeBangCreateForm;