const PillBtn = styled.button`
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  border-radius: 999px;
  font-size: 13px;
  line-height: 1;
  border: 1px solid
    ${(p) =>
      p.$variant === "danger" ? "#fecaca" : p.$variant === "ghost" ? "#e2e8f0" : "#cbd5e1"};
  background:
    ${(p) =>
      p.$variant === "danger" ? "#fff1f2" : p.$variant === "ghost" ? "#ffffff" : "#f8fafc"};
  color:
    ${(p) =>
      p.$variant === "danger" ? "#b91c1c" : p.$variant === "ghost" ? "#334155" : "#111827"};
  cursor: pointer;
  transition: background .15s ease, box-shadow .15s ease, transform .02s ease;
  box-shadow: 0 1px 0 rgba(0,0,0,.04);

  &:hover {
    background:
      ${(p) =>
        p.$variant === "danger" ? "#ffe4e6" : p.$variant === "ghost" ? "#f8fafc" : "#eef2ff"};
  }
  &:active { transform: translateY(1px); }
  &:disabled { opacity: .55; cursor: not-allowed; }
`;

const EditBtn = (props) => (
  <PillBtn {...props}><span></span><span></span></PillBtn>
);
const DeleteBtn = (props) => (
  <PillBtn {...props} $variant="danger"><span></span><span></span></PillBtn>
);