// src/pages/admin/AdminReviews.js
import React, { useState } from "react";
import api from "../../utils/api";
import {
  PageWrap, Header, Title, Sub,
  SearchCard, Row, Input,
  PrimaryBtn, DangerBtn, GhostBtn,
  ErrorText, InfoText,
} from "../../styles/components/admin/AdminCommonStyled";

//   /admin/content/{contentType}/{contentId}/reactivate
async function reactivateContent(type, id) {
  try {
    await api.put(`/admin/content/${type.toLowerCase()}/${id}/reactivate`);
  } catch (_) {
    await api.put(`/admin/content/${type.toUpperCase()}/${id}/reactivate`);
  }
}

export default function AdminReviews() {
  const [reviewId, setReviewId] = useState("");
  const [bulkIds, setBulkIds] = useState(""); // / 
  const [busy, setBusy] = useState(false);
  const [log, setLog] = useState([]);

  const addLog = (text) =>
    setLog((prev) => [{ ts: new Date().toISOString(), text }, ...prev].slice(0, 200));

  const parseIds = (s) =>
    s
      .split(/[\s,]+/)
      .map((x) => x.trim())
      .filter(Boolean)
      .map(Number)
      .filter((n) => Number.isInteger(n) && n > 0);

  const deactivateOne = async (id) => {
    if (!id) return alert(" ID .");
    if (!window.confirm(` #${id} () ?`)) return;
    setBusy(true);
    try {
      await api.delete(`/admin/reviews/${id}`);
      addLog(` : #${id}`);
    } catch (e) {
      addLog(` : #${id} - ${e?.response?.data?.message || e.message}`);
      alert(`: ${e?.response?.data?.message || e.message}`);
    } finally {
      setBusy(false);
    }
  };

  const reactivateOne = async (id) => {
    if (!id) return alert(" ID .");
    if (!window.confirm(` #${id} () ?`)) return;
    setBusy(true);
    try {
      await reactivateContent("review", id);
      addLog(` : #${id}`);
    } catch (e) {
      addLog(` : #${id} - ${e?.response?.data?.message || e.message}`);
      alert(`: ${e?.response?.data?.message || e.message}`);
    } finally {
      setBusy(false);
    }
  };

  const bulkDeactivate = async () => {
    const ids = parseIds(bulkIds);
    if (ids.length === 0) return alert("ID  .");
    if (!window.confirm(`${ids.length}  ?`)) return;
    setBusy(true);
    try {
      for (const id of ids) {
        try {
          await api.delete(`/admin/reviews/${id}`);
          addLog(` : #${id}`);
        } catch (e) {
          addLog(` : #${id} - ${e?.response?.data?.message || e.message}`);
        }
      }
      alert("  (   )");
    } finally {
      setBusy(false);
    }
  };

  const bulkReactivate = async () => {
    const ids = parseIds(bulkIds);
    if (ids.length === 0) return alert("ID  .");
    if (!window.confirm(`${ids.length}  ?`)) return;
    setBusy(true);
    try {
      for (const id of ids) {
        try {
          await reactivateContent("review", id);
          addLog(` : #${id}`);
        } catch (e) {
          addLog(` : #${id} - ${e?.response?.data?.message || e.message}`);
        }
      }
      alert("  (   )");
    } finally {
      setBusy(false);
    }
  };

  return (
    <PageWrap>
      <Header>
        <Title> </Title>
        <Sub>  /    .</Sub>
      </Header>

      {/*   */}
      <SearchCard>
        <Row>
          <Input
            value={reviewId}
            onChange={(e) => setReviewId(e.target.value)}
            placeholder=" ID"
            type="number"
            style={{ maxWidth: 220 }}
          />
          <DangerBtn disabled={busy} onClick={() => deactivateOne(Number(reviewId))}>
            {busy ? " " : ""}
          </DangerBtn>
          <PrimaryBtn disabled={busy} onClick={() => reactivateOne(Number(reviewId))}>
            {busy ? " " : ""}
          </PrimaryBtn>
        </Row>
      </SearchCard>

      {/*   */}
      <SearchCard>
        <details open>
          <summary style={{ cursor: "pointer", userSelect: "none", fontWeight: 600, marginBottom: 8 }}>
              (/ )
          </summary>
          <textarea
            value={bulkIds}
            onChange={(e) => setBulkIds(e.target.value)}
            rows={6}
            placeholder={")\n101,102,103\n\n201\n202\n203"}
            style={{
              width: "100%",
              border: "1px solid #e5e7eb",
              borderRadius: 10,
              padding: 10,
              outline: "none",
              fontFamily: "inherit",
              resize: "vertical",
              background: "#fff",
            }}
          />
          <Row style={{ marginTop: 8 }}>
            <DangerBtn disabled={busy} onClick={bulkDeactivate}>
              {busy ? " " : " "}
            </DangerBtn>
            <PrimaryBtn disabled={busy} onClick={bulkReactivate}>
              {busy ? " " : " "}
            </PrimaryBtn>
            <GhostBtn onClick={() => setBulkIds("")}></GhostBtn>
          </Row>
        </details>
      </SearchCard>

      {/*   */}
      <SearchCard>
        <div style={{ fontWeight: 600, marginBottom: 8 }}> </div>
        <div
          style={{
            maxHeight: 280,
            overflow: "auto",
            border: "1px dashed #e5e7eb",
            borderRadius: 12,
            padding: 10,
            background: "#fafafa",
          }}
        >
          {log.length === 0 && <InfoText>  .</InfoText>}
          {log.map((L, i) => (
            <div
              key={i}
              style={{
                fontFamily: "ui-monospace, SFMono-Regular, Menlo, Consolas, monospace",
                fontSize: 12,
                marginBottom: 4,
                whiteSpace: "pre-wrap",
              }}
            >
              <span style={{ color: "#6b7280" }}>[{L.ts}]</span> {L.text}
            </div>
          ))}
        </div>
      </SearchCard>
    </PageWrap>
  );
}
