// src/pages/admin/AdminUsers.js
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import api from "../../utils/api";
import AdminUserMemos from "./AdminUserMemos";
import {
  PageWrap, Header, Title, Sub,
  SearchCard, Row, Input, PrimaryBtn, GhostBtn, MutedBtn,
  Select, TableCard, Table, Th, Td, Actions,
  Pagination, PageInfo, ErrorText, InfoText, Empty,
} from "../../styles/components/admin/AdminCommonStyled";

const PAGE_SIZE = 10;

export default function AdminUsers() {
  const [page, setPage] = useState(0);
  const [q, setQ] = useState("");
  const [resp, setResp] = useState(null);
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState(null);
  const [busyId, setBusyId] = useState(null);
  const [memoUser, setMemoUser] = useState(null); // {id, email, name}

  async function fetchPage(p = 0) {
    setLoading(true);
    setErr(null);
    try {
      const { data } = await api.get("/admin/users", {
        params: { page: p, size: PAGE_SIZE /*, q: q.trim()*/ },
      });
      setResp(data);
      setPage(p);
    } catch (e) {
      setErr(e?.response?.data?.message || e.message || " ");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => { fetchPage(0); }, []);

  const content = resp?.content || [];
  const totalPages = resp?.totalPages ?? 0;

  // 
  const updateRole = async (userId, role, el) => {
    if (!role) return;
    setBusyId(userId);
    try {
      await api.put(`/admin/users/${userId}/role`, { role });
      await fetchPage(page);
    } catch (e) {
      alert(`  : ${e?.response?.data?.message || e.message}`);
    } finally {
      setBusyId(null);
      if (el) el.value = "";
    }
  };

  const updateStatus = async (userId, status, el) => {
    if (!status) return;
    setBusyId(userId);
    try {
      await api.put(`/admin/users/${userId}/status`, { status });
      await fetchPage(page);
    } catch (e) {
      alert(`  : ${e?.response?.data?.message || e.message}`);
    } finally {
      setBusyId(null);
      if (el) el.value = "";
    }
  };

  const suspendUser = async (userId, period, el) => {
    if (!period) return;
    if (!window.confirm(`   ${period} ?`)) {
      if (el) el.value = "";
      return;
    }
    setBusyId(userId);
    try {
      await api.put(`/admin/users/${userId}/suspend`, { period });
      await fetchPage(page);
    } catch (e) {
      alert(`  : ${e?.response?.data?.message || e.message}`);
    } finally {
      setBusyId(null);
      if (el) el.value = "";
    }
  };

  const onSearch = () => fetchPage(0);
  const onReset = () => { setQ(""); fetchPage(0); };

  return (
    <PageWrap>
      <Header>
        <Title> </Title>
        <Sub>//  ,   .</Sub>
      </Header>

      {/*   */}
      <SearchCard>
        <Row>
          <Input
            value={q}
            onChange={(e) => setQ(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && onSearch()}
            placeholder="/ "
          />
          <PrimaryBtn onClick={onSearch}></PrimaryBtn>
          <GhostBtn onClick={onReset}></GhostBtn>
        </Row>
      </SearchCard>

      {loading && <InfoText> </InfoText>}
      {err && <ErrorText>: {String(err)}</ErrorText>}

      {!loading && !err && (
        content.length === 0 ? (
          <Empty> </Empty>
        ) : (
          <>
            <TableCard>
              <Table>
                <thead>
                  <tr>
                    <Th w={260}>Email</Th>
                    <Th w={90}>Nickname</Th>
                    <Th w={80}>Role</Th>
                    <Th w={80}>Status</Th>
                    <Th w={90}></Th>
                    <Th></Th>
                  </tr>
                </thead>
                <tbody>
                  {content.map((u) => {
                    const created =
                      (u.createdAt && String(u.createdAt).slice(0, 10)) ||
                      (u.created_date && String(u.created_date).slice(0, 10)) ||
                      "-";
                    const role   = u.role || (u.roles && u.roles.join(", ")) || "-";
                    const status = u.status || (u.active ? "ACTIVE" : "INACTIVE");
                    const nick   = u.nickname || u.name || "-"; //   

                    return (
                      <tr key={u.userId}>
                        <Td w={260} ellipsis title={u.email}>{u.email}</Td>
                        <Td w={90} ellipsis title={nick}>{nick}</Td>
                        <Td w={80} ellipsis title={role}>{role}</Td>
                        <Td w={80}>{status}</Td>
                        <Td w={90}>{created}</Td>
                        <Td>
                           <Actions>
                            <PrimaryBtn
                              compact
                              onClick={() => setMemoUser({ id: u.userId, email: u.email, name: nick })}
                            >
                              
                            </PrimaryBtn>

                            <Select
                              compact
                              defaultValue=""
                              disabled={busyId === u.userId}
                              onChange={(e) => updateRole(u.userId, e.target.value, e.target)}
                              aria-label=" "
                            >
                              <option value="" disabled> </option>
                              <option value="ROLE_USER">ROLE_USER</option>
                              <option value="ROLE_ADMIN">ROLE_ADMIN</option>
                            </Select>

                            <Select
                              compact
                              defaultValue=""
                              disabled={busyId === u.userId}
                              onChange={(e) => updateStatus(u.userId, e.target.value, e.target)}
                              aria-label=" "
                            >
                              <option value="" disabled> </option>
                              <option value="ACTIVE">ACTIVE</option>
                              <option value="INACTIVE">INACTIVE</option>
                            </Select>

                            <Select
                              compact
                              defaultValue=""
                              disabled={busyId === u.userId}
                              onChange={(e) => suspendUser(u.userId, e.target.value, e.target)}
                              aria-label=" "
                            >
                              <option value="" disabled> </option>
                              <option value="THREE_DAYS">3</option>
                              <option value="SEVEN_DAYS">7</option>
                              <option value="TEN_DAYS">10</option>
                              <option value="THIRTY_DAYS">30</option>
                              <option value="SIX_MONTHS">6</option>
                              <option value="PERMANENT"></option>
                            </Select>

                            {/*  / :  > 0   */}
                            {Number(u.boardCount) > 0 && (
                              <PrimaryBtn
                                as={Link}
                                to={`/admin/users/${u.userId}/boards`}
                                compact
                                title={` ${u.boardCount.toLocaleString()}`}
                              >
                                 
                              </PrimaryBtn>
                            )}
                            {Number(u.commentCount) > 0 && (
                              <GhostBtn
                                as={Link}
                                to={`/admin/users/${u.userId}/comments`}
                                compact
                                title={` ${u.commentCount.toLocaleString()}`}
                              >
                                 
                              </GhostBtn>
                            )}
                          </Actions>
                        </Td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </TableCard>

            <Pagination>
              <MutedBtn disabled={page <= 0} onClick={() => fetchPage(page - 1)}>
                
              </MutedBtn>
              <PageInfo>{page + 1} / {totalPages || 1}</PageInfo>
              <MutedBtn disabled={page >= (totalPages - 1)} onClick={() => fetchPage(page + 1)}>
                
              </MutedBtn>
            </Pagination>
          </>
        )
      )}

      {/*    */}
      {memoUser && (
        <AdminUserMemos
          user={memoUser}
          onClose={() => setMemoUser(null)}
        />
      )}
    </PageWrap>
  );
}
